// æ›´æ–°ä¸ªäººä¿¡æ¯
const updateProfile = async () => {
  try {
    updating.value = true
    const response = await api.auth.updateProfile(editForm)
    if (response.data) {
      // åªæ›´æ–°Pinia storeä¸­çš„ç”¨æˆ·ä¿¡æ¯
